ht: 10rem;\n}\n.showName[data-v-990d2fe2]{\n    color:rgba(255,0,0,1);\n    font-size:0.6rem;\n    height: 1.7rem;\n    margin-top: 0.2rem;\n}\n.showPhone[data-v-990d2fe2]{\n    color:rgba(255,0,0,1);\n    font-size:0.6rem;\n    height: 2.2rem;\n    margin-top: 0.2rem;\n}\n.showPhone img[data-v-990d2fe2],.showName img[data-v-990d2fe2] {\n    width: 0.5rem;\n    margin-right: 0.2rem;\n}\n.table[data-v-990d2fe2] {\n        display: -webkit-box;\n        display: -ms-flexbox;\n        display: flex;\n        -webkit-box-pack: center;\n            -ms-flex-pack: center;\n                justify-content: center;\n        margin: 0.75rem auto 0.1rem;\n}\n.table_content[data-v-990d2fe2] {\n        display: -webkit-box;\n        display: -ms-flexbox;\n        display: flex;\n        -webkit-box-pack: center;\n            -ms-flex-pack: center;\n                justify-content: center;\n        margin: 0 auto 0.1rem;\n        font-size: 0.6rem;\n}\n.table_content > div[data-v-990d2fe2] {\n        height: 2.2rem;\n        line-height:2.2rem;\n        text-align:center;\n}\n.table > div[data-v-990d2fe2] {\n        height: 2.2rem;\n        line-height:2.2rem;\n        background:rgba(217,234,251,1);\n        text-align:center;\n}\n.name[data-v-990d2fe2],.phone[data-v-990d2fe2],.quanxian[data-v-990d2fe2],.setting[data-v-990d2fe2]{\n        color:rgba(51,51,51,1);\n        font-size:0.7rem;\n}\n.name[data-v-990d2fe2],.getname[data-v-990d2fe2] {\n        width:3.7175rem;\n        margin-right:0.05rem;\n        white-space: nowrap;\n        text-overflow: ellipsis;\n        overflow: hidden;\n}\n.phone[data-v-990d2fe2],.getphone[data-v-990d2fe2] {\n        width:4.9675rem;\n        margin-right:0.05rem;\n}\n.quanxian[data-v-990d2fe2],.getquanxian[data-v-990d2fe2] {\n        width:3.4675rem;\n        margin-right:0.05rem;\n}\n.setting[data-v-990d2fe2],.getsetting[data-v-990d2fe2] {\n        width:3.715rem;\n}\n.getname[data-v-990d2fe2],.getquanxian[data-v-990d2fe2] {\n        background:rgba(247,247,247,1);\n}\n.getphone[data-v-990d2fe2],.getsetting[data-v-990d2fe2] {\n        background:rgba(240,240,240,1);\n}\n.getsetting[data-v-990d2fe2] {\n        display: -webkit-box;\n        display: -ms-flexbox;\n        display: flex;\n        -webkit-box-pack: center;\n            -ms-flex-pack: center;\n                justify-content: center;\n        -webkit-box-align: center;\n            -ms-flex-align: center;\n                align-items: center;\n}\n.cancel[data-v-990d2fe2] {\n        width: 3rem;\n        height: 1.2rem;\n        line-height: 1.2rem;\n        color:rgba(255,255,255,1);\n        background:rgba(51,102,204,1);\n        border-radius:0.2rem;\n        font-size: 0.6rem;\n}\n.onAdd[data-v-990d2fe2] {\n        width: 14.75rem;\n        height: 2.5rem;\n        background:rgba(204,0,0,1);\n        -webkit-box-shadow:0rem 0rem 0.5rem 0rem rgba(60,50,4,0.2);\n                box-shadow:0rem 0rem 0.5rem 0rem rgba(60,50,4,0.2);\n        border-radius: 2.5rem;\n        margin: 0 auto;\n        text-align: center;\n        line-height: 2.5rem;\n        font-size:0.9rem;\n        font-family:Microsoft YaHei;\n        color:rgba(255,255,255,1);\n}\n", "", {"version":3,"sources":["/Users/shanshan/Desktop/vue-project/shenbing/src/components/jurisdiction/src/components/jurisdiction/Jurisdiction.vue"],"names":[],"mappings":";AAgRA;IACA,mBAAA;CACA;AACA;IACA,gBAAA;IACA,OAAA;IACA,QAAA;CACA;AAEA;IACA,YAAA;IACA,aAAA;IACA,4BAAA;CACA;AAGA;IACA,qBAAA;CACA;AAEA;IACA,YAAA;IACA,aAAA;IACA,4BAAA;CACA;AAEA;IACA,YAAA;IACA,aAAA;IACA,4BAAA;CACA;AAEA;IACA,gBAAA;IACA,eAAA;IACA,+BAAA;IACA,sBAAA;IACA,eAAA;IACA,mBAAA;IACA,oBAAA;IACA,iBAAA;IACA,4BAAA;IACA,0BAAA;IACA,sBAAA;CACA;AACA;IACA,gBAAA;IACA,eAAA;IACA,mBAAA;IACA,sBAAA;IACA,eAAA;IACA,mBAAA;IACA,oBAAA;IACA,iBAAA;IACA,4BAAA;IACA,0BAAA;IACA,sBAAA;CACA;AACA;IACA,gBAAA;IACA,eAAA;IACA,6BAAA;IACA,sBAAA;IACA,eAAA;IACA,mBAAA;IACA,oBAAA;IACA,iBAAA;IACA,4BAAA;IACA,0BAAA;IACA,sBAAA;CACA;AAEA;IACA,yBAAA;IACA,iBAAA;IACA,mBAAA;CACA;AAEA;IACA,aAAA;IACA,+BAAA;IACA,sBAAA;IACA,sBAAA;IACA,sBAAA;IACA,uBAAA;IACA,oBAAA;CACA;AAEA;IACA,kBAAA;IACA,uBAAA;IACA,sBAAA;IACA,oBAAA;IAAA,oBAAA;IAAA,aAAA;IACA,0BAAA;QAAA,uBAAA;YAAA,oBAAA;CACA;AAEA;IACA,uBAAA;IACA,kBAAA;IACA,sBAAA;IACA,oBAAA;IAAA,oBAAA;IAAA,aAAA;IACA,0BAAA;QAAA,uBAAA;YAAA,oBAAA;CACA;AACA;IACA,uBAAA;IACA,kBAAA;IACA,sBAAA;IACA,oBAAA;IAAA,oBAAA;IAAA,aAAA;IACA,0BAAA;QAAA,uBAAA;YAAA,oBAAA;CACA;AACA;IACA,uBAAA;IACA,kBAAA;IACA,oBAAA;CACA;AAEA;IACA,eAAA;CACA;AAEA;IACA,cAAA;IACA,qBAAA;IACA,oBAAA;CACA;AAEA;IACA,sBAAA;IACA,mBAAA;CACA;AAEA;IACA,qBAAA;IACA,sBAAA;IACA,cAAA;IACA,eAAA;IACA,8BAAA;IACA,kBAAA;CACA;AAEA;IACA,aAAA;IACA,aAAA;IACA,qBAAA;IACA,mBAAA;CACA;AAEA;IACA,iBAAA;CACA;AACA;IACA,cAAA;CACA;AACA;IACA,cAAA;CACA;AAEA;IACA,sBAAA;IACA,iBAAA;IACA,eAAA;IACA,mBAAA;CACA;AACA;IACA,sBAAA;IACA,iBAAA;IACA,eAAA;IACA,mBAAA;CACA;AACA;IACA,cAAA;IACA,qBAAA;CACA;AAEA;QACA,qBAAA;QAAA,qBAAA;QAAA,cAAA;QACA,yBAAA;YAAA,sBAAA;gBAAA,wBAAA;QACA,4BAAA;CACA;AACA;QACA,qBAAA;QAAA,qBAAA;QAAA,cAAA;QACA,yBAAA;YAAA,sBAAA;gBAAA,wBAAA;QACA,sBAAA;QACA,kBAAA;CACA;AACA;QACA,eAAA;QACA,mBAAA;QACA,kBAAA;CACA;AACA;QACA,eAAA;QACA,mBAAA;QACA,+BAAA;QACA,kBAAA;CACA;AACA;QACA,uBAAA;QACA,iBAAA;CACA;AACA;QACA,gBAAA;QACA,qBAAA;QACA,oBAAA;QACA,wBAAA;QACA,iBAAA;CACA;AACA;QACA,gBAAA;QACA,qBAAA;CACA;AACA;QACA,gBAAA;QACA,qBAAA;CACA;AACA;QACA,eAAA;CACA;AAEA;QACA,+BAAA;CACA;AACA;QACA,+BAAA;CACA;AAEA;QACA,qBAAA;QAAA,qBAAA;QAAA,cAAA;QACA,yBAAA;YAAA,sBAAA;gBAAA,wBAAA;QACA,0BAAA;YAAA,uBAAA;gBAAA,oBAAA;CACA;AAEA;QACA,YAAA;QACA,eAAA;QACA,oBAAA;QACA,0BAAA;QACA,8BAAA;QACA,qBAAA;QACA,kBAAA;CACA;AAEA;QACA,gBAAA;QACA,eAAA;QACA,2BAAA;QACA,2DAAA;gBAAA,mDAAA;QACA,sBAAA;QACA,eAAA;QACA,mBAAA;QACA,oBAAA;QACA,iBAAA;QACA,4BAAA;QACA,0BAAA;CACA","file":"Jurisdiction.vue","sourcesContent":["<template>\n<div>\n    <div class=\"jurisdiction\">\n        <div class=\"addMaster\" v-show=\"showAddMaster\">\n            <div class=\"addMaster_box\">\n                <div><span></span>添加管理员</div>\n                <div><input type=\"text\" v-model=\"username\" placeholder=\"请输入姓名\" @keyup=\"onkeyupName\"></div>\n                <div class=\"showName\"><span v-show=\"showName\"><img :src=\"noIcon\">请输入姓名</span></div>\n                <div><input type=\"number\" placeholder=\"请输入手机号码\" v-model=\"userphone\" @keyup=\"onkeyupPhone\"></div>\n                <div class=\"showPhone\"><span v-show=\"showPhone\"><img :src=\"noIcon\">手机号格式有误，请重新输入。</span></div>\n                <div :class=\"[changeColor?'onAddMaster':'onAddMasterBlue']\" @click=\"onAddMaster()\">确认</div>\n                <div class=\"onNoAddMaster\" @click=\"onNoAddMaster()\">取消</div>\n            </div>\n        </div>\n\n        <div class=\"delMaster\" v-show=\"showDelMaster\">\n            <div class=\"delMaster_box\">\n                <div><span></span>取消管理员</div>\n                <div class=\"detail_msg\">\n                    姓名：<span>{{getusername}}</span>\n                    <br /><br />\n                    电话：<span>{{getuserphone}}</span>\n                </div>\n                <div class=\"onDelMaster\" @click=\"onDelMaster()\">确认</div>\n                <div class=\"onNoDelMaster\" @click=\"onNoDelMaster()\">取消</div>\n            </div>\n        </div>\n\n        <div class=\"noMaster\" v-show=\"showNoMaster\">\n            <div class=\"noMaster_box\">\n                <div><span></span><div>{{changeTitle}}</div></div>\n                <div><img class=\"iconYes\" :src=\"YesIcon\">{{getBegin}}<span class=\"name_phone\"> {{getusername.length>4?(getusername.substring(0,4)+'...'):getusername}}({{getuserphone}}) </span>{{getEnd}}</div>\n                <div class=\"onKnowDelMaster\" @click=\"onKnowDelMaster()\">知道了</div>\n            </div>\n        </div>\n\n        <div class=\"table\">\n            <div class=\"name\">姓名</div>\n            <div class=\"phone\">电话</div>\n            <div class=\"quanxian\">权限</div>\n            <div class=\"setting\">设置</div>\n        </div>\n        <div class=\"itemBox\">\n            <div v-for=\"(item,index) in itemList\" class=\"table_content\" :key=\"index\">\n                <div class=\"getname\">{{item.name?item.name:'待注册'}}</div>\n                <div class=\"getphone\">{{item.phone}}</div>\n                <div class=\"getquanxian\">{{item.type ==1?'负责人':'管理员'}}</div>\n                \n                <div class=\"getsetting\" v-if=\"item.type==2\">\n                    <div class=\"cancel\" @click=\"onCancel(item.phone,item.companyId,item.name)\">取消权限</div>\n                </div>\n                <div class=\"getsetting\" v-if=\"item.type==1\">\n                </div>\n            </div>\n        </div>\n        <div class=\"onAdd\" @click=\"add()\">添加管理员</div>\n    </div>\n</div>\n    \n\n</template>\n\n<script>\nimport API from \"@/serve/index.js\"\n    export default {\n        created() {\n            this.getMessage();  \n        },\n        mounted() {\n            var qrcodeId = this.qrcodeId;\n            if(qrcodeId) {\n                API('get','/yjgcrest/feiyan/companyPersonList',{'qrcodeId':qrcodeId}).then(result => {\n                    if (result.data.status == 200) { //成功\n                        this.mobile = result.data.data.mobile\n                    }\n                })\n            };\n            // this.getUserRole()\n            window.onresize = () =>{\n                return(()=>{\n                    this.showHeight = document.documentElement.clientHeight || document.body.clientHeight;\n                })()\n            }\n        },\n        watch: {\n            showHeight:function() {\n                if(this.docmHeight <= this.showHeight) {\n                    window.scrollTo(0,0);\n                }\n            }\n        },\n        props:{\n            companyNo: {\n                type:String\n            },\n            qrcodeId: {\n                type:String\n            }\n        },\n        data() {\n            return {\n                showPhone: false,\n                showName: false,\n                showAddMaster: false,\n                showDelMaster: false,\n                showNoMaster: false,\n                itemList: [],\n                username: '',\n                userphone: '',\n                getusername:'',\n                getuserphone:'',\n                companyId: '',\n                changeTitle: '',\n                getBegin: '',\n                getMobile:'',\n                getEnd: '',\n                changeColor:true,\n                noIcon:require(\"../../../static/image/no_icon.png\"),\n                YesIcon:require(\"../../../static/image/yes_icon.png\"),\n                docmHeight: document.documentElement.clientHeight ||document.body.clientHeight,\n                showHeight: document.documentElement.clientHeight ||document.body.clientHeight,\n            }\n        },\n        methods: {\n            getUserRole:function(){\n                var that = this;\n                var uertel = this.$util.getCookie('userTel');\n                var query = {\n                    'channelCodeSe':this.$util.getCookie('channelCodeSec') ? this.$util.getCookie('channelCodeSec') : '',\n                }\n                if(!uertel||uertel==''){\n                    alert('未登录或登录失效，请重新登录')\n                    this.$router.push({path:'/verify',query:query});\n                    return;\n                }else{\n                    API('get','/yjgcrest/feiyan/mobileexists?mobile='+ uertel).then( res => {\n                       \n                        if(!res.data||res.data.code != '000001' || res.data.data.exists != 1){\n                            that.$router.push({path:'/verify',query:query});\n                            return;\n                        }\n                    }).catch( error => {\n                       console.log(error)\n                    })\n                }\n            },\n            onkeyupName(){\n                var name = (this.username).trim();\n                var phone = (this.userphone).trim();\n                if(name.length > 0) {\n                    this.changeColor = false;\n                    this.showName = false;\n                } else if(name.length == 0 && phone.length == 0) {\n                    this.changeColor = true;\n                }\n            },\n            onkeyupPhone(){\n                var name = (this.username).trim();\n                var phone = (this.userphone).trim();\n                if(phone.length> 0) {\n                    this.changeColor = false;\n                    this.showPhone = false;\n                } else if(name.length == 0 && phone.length == 0) {\n                    this.changeColor = true;\n                }\n            },\n            onKnowDelMaster(){\n                this.getMessage();\n                this.showNoMaster = false;\n            },\n            add() {\n                this.showAddMaster = true,\n                this.showPhone = false;\n                this.changeColor = true;\n                this.showName = false;\n                this.username = '';\n                this.userphone = '';\n            },\n            onNoAddMaster() {\n                this.showAddMaster = false;\n            },\n            onCancel(phone,companyId,name){\n                this.showDelMaster = true;\n                this.companyId = companyId;\n                this.getusername = name;\n                this.getuserphone = phone;\n            },\n            onDelMaster(){\n                var phone = this.getuserphone;\n                var companyId = this.companyId;\n              \n                if(phone && companyId) {\n                        API(\"get\",\"/yjgcrest/feiyan/delCompanyPerson?companyId=\" + companyId + \"&personMobile=\" + phone ).then(response => {\n                     \n\n                        if(response.status == 200 && response.data.status == 200) {\n                            this.getBegin = '已取消';\n                            this.getEnd = '的管理员身份。'\n                            this.showDelMaster = false;\n                            this.showNoMaster = true;\n                            this.changeTitle = '取消管理员';\n                        }\n\n                    }).catch(function(error){\n                        console.log(error);\n                    })\n                }\n            },\n            onNoDelMaster(){\n                this.showDelMaster = false\n            },\n            onAddMaster(){\n                var name = (this.username).trim();\n                var phone = (this.userphone).trim();\n                window.scrollTo(0,0);\n\n                if(!name) {\n                    this.showName = true\n                }\n\n                if(!phone) {\n                    this.showPhone = true\n                }\n\n                if (phone && (!(/^[1][34578]\\d{9}$/).test(phone) || !(/^[1-9]\\d*$/).test(phone) || phone.length !== 11)) {\n                    this.showPhone = true;\n                    return\n                } \n\n                if(name && phone) {\n                        API(\"get\",\"/yjgcrest/feiyan/addAdministrator?companyNo=\"+ this.companyNo + \"&personMobile=\" + phone + \"&personName=\" + name ).then(response => {\n                        if(response.status == 200 && response.data.status == 200) {\n                            this.changeTitle = '添加管理员';\n                            this.showAddMaster = false;\n                            this.showNoMaster = true;\n\n                            var status = response.data.data.resultCode;\n                            this.getusername = name;\n                            this.getuserphone = phone;\n                            if(status == '0000') {\n                                this.getBegin = '已添加';\n                                this.getEnd = '为管理员身份，并发送短信提醒。'\n                            } else if(status == '0001'){\n                                this.getBegin = '';\n                                this.getEnd = '已经是管理员身份，不可重复添加。'\n                            } else if(status == '0002') {\n                                this.getBegin = '';\n                                this.getEnd = '是企业负责人，不可添加为管理员'\n                            }\n                        }\n                    }).catch(function(error){\n                        console.log(error);\n                    })\n                }\n            },\n            getMessage(){\n                API(\"get\",\"/yjgcrest/feiyan/getAdminList?curComNo=\"+ this.companyNo).then(response => {\n                    if(response.status == 200 && response.data.status == 200){\n                        var list = response.data.data\n                        if(list){\n                            this.itemList = list\n                        }\n                    }\n                }).catch(function(error){\n                    console.log(error);\n                })\n            }\n        }\n    }\n</script>\n\n<style scoped>\n.jurisdiction {\n    position: relative;\n}\n.addMaster,.delMaster,.noMaster{\n    position: fixed;\n    top: 0;\n    left: 0;\n}\n\n.addMaster {\n    width: 100%;\n    height: 100%;\n    background: rgba(0,0,0,0.8);\n}\n\n\n.itemBox {\n    margin-bottom:1.6rem;\n}\n\n.delMaster {\n    width: 100%;\n    height: 100%;\n    background: rgba(0,0,0,0.8);\n}\n\n.noMaster {\n    width: 100%;\n    height: 100%;\n    background: rgba(0,0,0,0.8);\n}\n\n.onAddMaster{\n    width: 14.75rem;\n    height: 2.5rem;\n    background:rgba(204,204,204,1);\n    border-radius: 2.5rem;\n    margin: 0 auto;\n    text-align: center;\n    line-height: 2.5rem;\n    font-size:0.9rem;\n    font-family:Microsoft YaHei;\n    color:rgba(255,255,255,1);\n    margin-bottom: 1.6rem;\n}\n.onAddMasterBlue{\n    width: 14.75rem;\n    height: 2.5rem;\n    background:#2D78E0;\n    border-radius: 2.5rem;\n    margin: 0 auto;\n    text-align: center;\n    line-height: 2.5rem;\n    font-size:0.9rem;\n    font-family:Microsoft YaHei;\n    color:rgba(255,255,255,1);\